/* eslint-disable no-underscore-dangle */
import React from 'react';
import PropTypes from 'prop-types';
import useSound from 'use-sound';

import effectsAsset, {
  spriteMap as effectsMap,
} from '../../episodes/0/0/assets/soundsButton';

const Context = React.createContext();

const SoundControllerProvider = ({
  children,
  effectsURL,
  effectsMap: spriteRaw,
  ambientURL,
}) => {
  const storageMuted = true;

  const sprite = Object.keys(spriteRaw).reduce((obj, key) => {
    const [start, end] = spriteRaw[key];
    return {
      ...obj,
      [key]: [start, end - start],
    };
  }, {});

  const numToLoad = [effectsURL, ambientURL].filter(Boolean).length;

  const [loaded, setLoaded] = React.useState(0);
  const [muted, setMuted] = React.useState(storageMuted);

  const handleLoaded = () => setLoaded((val) => Math.min(numToLoad, val + 1));
  const handleError = (...args) => console.warn('Error', ...args);
  const handlePlayError = (...args) => console.warn('PlayError', ...args);
  const handleLoadError = (...args) => console.warn('LoadError', ...args);

  const soundEvents = {
    onerror: handleError,
    onplayerror: handlePlayError,
    onloaderror: handleLoadError,
    onload: handleLoaded,
  };

  const [playEffect, effects] = useSound(effectsURL, {
    sprite,
    volume: 0.75,
    ...soundEvents,
  });

  const [playAmbient, ambient] = useSound(ambientURL, {
    volume: 1,
    ...soundEvents,
  });

  const audioContext = {
    numToLoad,
    loaded: loaded === numToLoad,
    muted,
    setMuted,
    effects: {
      ...effects,
      play: (eventName) => {
        if (muted || !sprite[eventName]) return;
        playEffect({ id: eventName });
      },
    },
    ambient: {
      ...ambient,
      play: (...args) => {
        if (muted) return;
        playAmbient(...args);
      },
    },
  };

  return (
    <Context.Provider value={audioContext}>
      {children}
    </Context.Provider>
  );
};

SoundControllerProvider.propTypes = {
  children: PropTypes.node.isRequired,
  effectsURL: PropTypes.string,
  effectsMap: PropTypes.objectOf(PropTypes.array),
  ambientURL: PropTypes.string,
};

SoundControllerProvider.defaultProps = {
  effectsURL: effectsAsset,
  effectsMap,
  ambientURL: '',
};

export default SoundControllerProvider;

export const useSoundController = () => React.useContext(Context);
