import React from 'react';

import { useSoundController } from './Context';

const noop = () => { };

const useSoundTrigger = ({
  onClick,
  clickSound,
  onMouseEnter,
  enterSound,
  onMouseLeave,
  outSound,
  ...props
}) => {
  const { effects: { play = noop } = {} } = useSoundController() || {};

  const handleClick = React.useCallback((...args) => {
    play(clickSound || 'click');
    if (typeof onClick === 'function') onClick(...args);
  }, [onClick, clickSound]);

  const handleMouseEnter = React.useCallback((...args) => {
    play(enterSound || 'enter');
    if (typeof onMouseEnter === 'function') onMouseEnter(...args);
  }, [onMouseEnter, enterSound]);

  const handleMouseLeave = React.useCallback((...args) => {
    play(enterSound || 'leave');
    if (typeof onMouseLeave === 'function') onMouseLeave(...args);
  }, [onMouseLeave, enterSound]);

  return {
    ...props,
    onClick: handleClick,
    onMouseEnter: handleMouseEnter,
    onMouseLeave: handleMouseLeave,
  };
};

export default useSoundTrigger;
