import React from 'react';
import PropTypes from 'prop-types';
import AniLink from 'gatsby-plugin-transition-link/AniLink';

import theme from '../themes/data';
import classes from './Link.module.scss';

import useSoundTrigger from './SoundController/useSoundTrigger';

const Link = ({
  children,
  ...props
}) => {
  const {
    to,
    color,
    className,
    activeClassName,
  } = props;

  const soundProps = useSoundTrigger(props);

  return (
    <AniLink
      data-mk-link
      paintDrip
      hex={theme[color] || color}
      to={to}
      onClick={soundProps.onClick}
      onMouseEnter={soundProps.onMouseEnter}
      className={[
        className,
        classes.root,
        classes[color],
      ].filter(Boolean).join(' ')}
      activeClassName={activeClassName || classes.active}
    >
      {children}
    </AniLink>
  );
};

Link.displayName = 'MKLink';

Link.propTypes = {
  children: PropTypes.node.isRequired,
  to: PropTypes.string.isRequired,
  color: PropTypes.string,
  className: PropTypes.string,
  activeClassName: PropTypes.string,
  onClick: PropTypes.func,
  onMouseEnter: PropTypes.func,
};

Link.defaultProps = {
  color: 'primary',
  className: null,
  activeClassName: null,
  onClick: () => { },
  onMouseEnter: () => { },
};

export default Link;
