import React from 'react';
import PropTypes from 'prop-types';
import { createUseStyles } from 'react-jss';

import { useSoundController } from './Context';
import Button from '../Button';

const useStyles = createUseStyles({
  root: {},
}, { name: 'SoundController' });

const SoundController = ({ className, ...props }) => {
  const classes = useStyles(props);
  const controller = useSoundController() || {};

  const {
    muted,
    setMuted,
    loaded,
  } = controller;

  return (
    <div className={[className, classes.root].filter(Boolean).join(' ')}>
      <Button
        disabled={!loaded}
        onClick={() => setMuted(!muted)}
      >
        {`Sound is ${muted ? 'off' : 'on'}`}
      </Button>
    </div>
  );
};

SoundController.propTypes = {
  className: PropTypes.string,
};

SoundController.defaultProps = {
  className: null,
};

export default SoundController;

export { useSoundController };

export { default as Provider } from './Context';
